<template>
  <div class="camera-modal">
    <div id="camera">
      <v-snackbar v-model="textMsg" :timeout="timeoutMsg" absolute top centered>
        <span style="text-align:center;padding-left: 30px;">Posicione o documento dentro da caixa</span>
      </v-snackbar>
      <canvas id="camera--sensor"></canvas>
      <video id="camera--view" ref="video" class="camera-stream" autoplay playsinline/>
    </div>
    <div class="camera-modal-container"></div>
    <v-btn class="fixedbutton take-picture-button" @click="capture" color="pink" dark fab v-if="!carregando">
      <v-icon>camera</v-icon>
    </v-btn>
    <v-progress-circular indeterminate color="primary" v-if="carregando" class="fixedbutton2"><span>Processando...</span></v-progress-circular>
    <v-snackbar style="bottom: 69px;" v-model="rejectError" :timeout="timeoutMsg" absolute bottom centered>
        <span style="text-align:center;padding-left: 30px;">{{textError}}</span>
      </v-snackbar>
    <v-snackbar style="bottom: 69px;" v-model="snackbar" :timeout="timeout" absolute left shaped>
      <a :href="text" target="_blank">Acessar URL da imagem</a>
      <v-btn color="blue" text @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import adapter from "webrtc-adapter";

export default {
  data() {
    return {
      mediaStream: null,
      snackbar: false,
      text: "",
      timeout: 120000,
      timeoutMsg: 10000,
      tipoDoc: null,
      carregando: false,
      textMsg: false,
      rejectError:false,
      textError: ""
    };
  },
  created() {
    /**
     * Pega como referncia o parametro da url
     * ex: http://localhost:8181/camera?doc=comprovante
     */
    const queryString = window.location.search;
    this.tipoDoc = this.parseQuery(queryString);
  },
  mounted() {
    var canvas = document.getElementById("camera--sensor");
    if (canvas.getContext) {
      var ctx = canvas.getContext("2d");

      console.log("screen:", screen.width, screen.height);
      console.log("canvas:", canvas.width, canvas.height);
      ctx.shadowBlur = blur;
      ctx.shadowOffsetX = 0;
      ctx.shadowOffsetY = 0;

      ctx.fillStyle = "#000000de";
      ctx.shadowColor = "#000000de";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      switch (this.tipoDoc.doc) {
        case "identificacao":
          // Documento CHN ou RG
          ctx.clearRect(canvas.width / 2 - 50 / 2, 30, 50, 75);
          ctx.strokeStyle = "rgb(0, 162, 19)";
          ctx.strokeRect(canvas.width / 2 - 50 / 2, 30, 50, 75);
          this.textMsg = true;
          break;
        case "comprovante":
          //Comprovante residencial / Comprovante de renda
          ctx.clearRect(canvas.width / 2 - 60 / 2, 20, 60, 100);
          ctx.strokeStyle = "rgb(0, 162, 19)";
          ctx.strokeRect(canvas.width / 2 - 60 / 2, 20, 60, 100);
          this.textMsg = true;
          break;
        default:
           this.rejectError = true;
           this.textError = "Informe o tipo de documento para scanner";
          break;
      }
      this.showMsg();
    }

    var constraints = {
      audio: false,
      video: {
        facingMode: "environment" // https://developer.mozilla.org/en-US/docs/Web/API/MediaTrackConstraints/facingMode
      }
    };
    try {
      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        try {
          navigator.mediaDevices
          .getUserMedia(constraints)
            .then(mediaStream => {
              this.mediaStream = mediaStream;
              this.$refs.video.srcObject = mediaStream;
              this.$refs.video.play();
            })
        } catch (error) {
          this.rejectError = true;
          this.textError = error;
        }
      } else if (navigator.getUserMedia) {
        try {
          // Standard
          navigator.getUserMedia(
          { video: true },
            function(mediaStream) {
              this.mediaStream = mediaStream;
              this.$refs.video.srcObject = mediaStream;
              this.$refs.video.play();
            }
          );
        } catch (error) {
          this.rejectError = true;
          this.textError = error;
        }

      } else if (navigator.webkitGetUserMedia) {
        // WebKit-prefixed
        try {
          navigator.webkitGetUserMedia(
          { video: true },
            function(mediaStream) {
              this.mediaStream = mediaStream;
              this.$refs.video.srcObject = window.webkitURL.createObjectURL(
                mediaStream
              );
              this.$refs.video.play();
            }
          );
        } catch (error) {
         this.rejectError = true;
         this.textError = error;
        }
      } else if (navigator.mozGetUserMedia) {
        try {
           navigator.mozGetUserMedia(
          { video: true },
            function(mediaStream) {
              this.mediaStream = mediaStream;
              this.$refs.video.srcObject = window.URL.createObjectURL(
                mediaStream
              );
              this.$refs.video.play();
            }
          );
        } catch (error) {
         this.rejectError = true;
         this.textError = error;
        }
      } else {
        try {
          navigator.mediaDevices
            .getUserMedia(constraints)
            .then(function success(mediaStream) {
              this.mediaStream = mediaStream;
              this.$refs.video.srcObject = window.webkitURL.createObjectURL(
                mediaStream
            );
            this.$refs.video.play();
          });
        } catch (error) {
          this.rejectError = true;
          this.textError = error;
        }
      }
    } catch (error) {
      this.rejectError = true;
      this.textError = "Navegador incopativel.";
    }
  },
  methods: {
    ...mapActions(["uploadImagem1"]),
    showMsg() {
      setInterval(() => {
        this.textMsg = true;
      }, 5000);
    },
    capture() {
      this.carregando = true;
      this.rejectError = false;
      const mediaStreamTrack = this.mediaStream.getVideoTracks()[0];
      const imageCapture = new window.ImageCapture(mediaStreamTrack);
      return imageCapture.takePhoto().then(blob => {
        this.uploadImagem1({
          id: new Date().getTime(),
          campo: "documentofoto1",
          file: blob
        }).then(retorno => {
          this.text = retorno.config + "/" + retorno.path.img;
          this.snackbar = true;
          this.carregando = false;
        }).catch(err=>{
          this.rejectError = true;
          console.log(err.data.error)
          this.textError = err.data.error;
        });
      });
    },
    parseQuery(queryString) {
      var query = {};
      var pairs = (queryString[0] === "?"
        ? queryString.substr(1)
        : queryString
      ).split("&");
      for (var i = 0; i < pairs.length; i++) {
        var pair = pairs[i].split("=");
        query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || "");
      }
      return query;
    }
  },
  destroyed() {
    const tracks = this.mediaStream.getTracks();
    tracks.map(track => track.stop());
  }
};
</script>

