import Axios from "axios";
import { BASE_URL, BASE_URL_WEBSITE } from "@/config/config";
import * as mutation from "./../mutation-types";
import moment from "moment";

const state = {
  leadForm: {
    id: null,
    nome: "",
    cpf: "",
    email: "",
    profissao: null,
    telefone1: "",
    telefone2: "",
    dataNascimento: null,
    senhaINSS: "",
    tipoDocumento: null,
    fileFrente: null,
    fileVerso: null,
    fileComprovanteBancario: null,
    fileComprovanteResidencial: null,
    ip: null
  },
  token: null,
  socket: [],
  loading: false
};

const getters = {
  leadForm: state => state.leadForm
};

const mutations = {
  [mutation.SET_LEAD_FORM](state, payload) {
    if (payload && payload.dataNascimento && payload.dataNascimento.indexOf('T') !== -1) {
      payload.dataNascimento = payload.dataNascimento.split('T')[0].split('-').reverse().join('/');
    }
    state.leadForm = payload;
  },
  [mutation.SET_TOKEN](state, payload) {
    state.token = payload;
  },
  [mutation.SET_IP](state, payload) {
    state.leadForm.ip = payload;
  },
  [mutation.ADD_SOCKET](state, payload) {
    state.socket.push({ id: payload });
  },
  [mutation.IS_LOADING_LEAD_FORM](state, payload) {
    state.loading = payload;
  }
};

const actions = {
  sendLeadForm: ({ commit }, payload) =>
    new Promise((resolve, reject) => {
      const onSuccess = response => {
        commit(mutation.SET_LEAD_FORM, response.data.lead);

        return resolve(response.data);
      };

      const onError = error => {
        console.log(error);
        var r = confirm(
          "Sua sessão já expirou, por favor tente um novo cadastro novamente!"
        );
        if (r == true) {
          window.open(BASE_URL_WEBSITE);
        } else {
          window.open(BASE_URL_WEBSITE);
        }
        return reject(error);
      };

      payload.urlOrigem = (window.location != window.parent.location) ? document.referrer : document.location.href;

      Axios.defaults.headers["authorization"] = "Baear " + state.token;
      Axios.post(`${BASE_URL}/v1/leads`, payload, {
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then(onSuccess)
        .catch(onError);
    }),

  getLeadForm: ({ commit }, obj) =>
    new Promise((resolve, reject) => {
      const onSuccess = response => {
        commit(mutation.SET_LEAD_FORM, response.data.lead);
        return resolve(response.data.lead);
      };

      const onError = error => {
        console.log(error);
        var r = confirm(
          "Sua sessão já expirou, por favor tente um novo cadastro novamente!"
        );
        if (r == true) {
          window.open(BASE_URL_WEBSITE);
        } else {
          window.open(BASE_URL_WEBSITE);
        }

        return reject(error);
      };

      commit(mutation.SET_TOKEN, obj.token);

      Axios.defaults.headers["authorization"] = "Baear " + state.token;
      Axios.get(`${BASE_URL}/v1/leads/${obj.id}`, {
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then(onSuccess)
        .catch(onError);
    }),

  getIp: ({ commit }) =>
    new Promise((resolve, reject) => {
      const onSuccess = response => {
        let ip = response.data.ip ? response.data.ip : "localhost";
        commit(mutation.SET_IP, ip);
        return resolve(ip);
      };

      const onError = error => {
        commit(mutation.SET_IP, []);
        console.log(error);
        var r = confirm(
          "Sua sessão já expirou, por favor tente um novo cadastro novamente!"
        );
        if (r == true) {
          window.open(BASE_URL_WEBSITE);
        } else {
          window.open(BASE_URL_WEBSITE);
        }
        return reject(error);
      };

      Axios.get(`${BASE_URL}/v1/leads/ip`, {
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then(onSuccess)
        .catch(onError);
    }),

  updateLeadForm: ({ commit }, payload) =>
    new Promise((resolve, reject) => {
      const onSuccess = response => {
        commit(mutation.SET_LEAD_FORM, response.data.Lead);
        return resolve(response.data);
      };

      const onError = error => {
        console.log(error);
        var r = confirm(
          "Sua sessão já expirou, por favor tente um novo cadastro novamente!"
        );
        if (r == true) {
          window.open(BASE_URL_WEBSITE);
        } else {
          window.open(BASE_URL_WEBSITE);
        }
        return reject(error);
      };

      payload = Object.assign({}, payload);
      // Convert data BR to US
      if ( payload.dataNascimento ) {
        payload.dataNascimento = payload.dataNascimento.split("/").reverse().join("-");
      }

      Axios.defaults.headers["authorization"] = "Baear " + state.token;
      Axios.put(`${BASE_URL}/v1/leads/${payload.id}`, payload, {
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then(onSuccess)
        .catch(onError);
    }),

  uploadFileForm: ({ commit }, payload) =>
    new Promise((resolve, reject) => {
      const onSuccess = response => {
        return resolve(response.data);
      };

      const onError = error => {
        return reject(error);
      };

      Axios.defaults.headers["authorization"] = "Baear " + state.token;
      Axios.post(
        `${BASE_URL}/v1/leads/${payload.id}/complete/${payload.tipoDocumento}`,
        payload.images,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      )
        .then(onSuccess)
        .catch(onError);
    }),

    uploadImagem: ({ commit }, payload) =>
    new Promise((resolve, reject) => {
      const onSuccess = response => {
        return resolve(response.data);
      };

      const onError = error => {
        return reject(error);
      };
      console.log('uploading imagem', payload.file);
      var data = new FormData();
      data.append("file", payload.file);
      Axios.defaults.headers["authorization"] = "Baear " + state.token;
      Axios.post(`${BASE_URL}/v1/leads/${payload.id}/upload/doc/${payload.campo}`, data,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      )
        .then(onSuccess)
        .catch(onError);
    }),

    uploadImagem1: ({ commit }, payload) =>
    new Promise((resolve, reject) => {
      const onSuccess = response => {
        return resolve({config : BASE_URL, path : response.data});
      };

      const onError = error => {
        return reject(error.response);
      };
      console.log('uploading imagem', payload.file);
      var data = new FormData();
      data.append("file", payload.file);
      Axios.post(`${BASE_URL}/v1/teste/${payload.id}/upload/doc/${payload.campo}`, data,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      )
        .then(onSuccess)
        .catch(onError);
    }),

  addSocket: ({ commit }, payload) => {
    commit(mutation.ADD_SOCKET, payload);
  },

  updateForm: ({ commit }, payload) => {
    commit(mutation.SET_LEAD_FORM, payload);
  }
};

export default {
  state,
  actions,
  getters,
  mutations
};
