<template>
  <ValidationObserver ref="observer" v-slot="{ validate, reset }">
    <v-form class="form" method="post" autocomplete="on" :action="urlNext" target="_blank" 
      @submit="checkForm">
      <div class="pb-3">
        <v-progress-linear :value="30" color="green lighten-2" height="20" rounded>
          <strong>30%</strong>
        </v-progress-linear>
      </div>
      <input type="hidden" name="urlOrigem" :value="urlOrigem" />
      <input type="hidden" name="statusCadastro" :value="1" />

      <ValidationProvider
        v-slot="{ errors, valid }"
        name="Seu Nome Completo"
        rules="required|min:3|max:60"
      >
        <v-text-field
          v-model="form.nome"
          :success="valid"
          :error-messages="errors"
          :counter="60"
          label="Seu Nome Completo"
          placeholder="Digite seu nome"
          class="data-hj-whitelist mt-8"
          outlined
          required
          id="nome"
          name="nome"
          data-hj-whitelist
        ></v-text-field>
      </ValidationProvider>
      <ValidationProvider
        v-slot="{ errors, valid }"
        name="Telefone/WhatsApp"
        rules="required|min:15"
      >
        <v-text-field
          v-model="form.telefone1"
          :success="valid"
          :error-messages="errors"
          :counter="15"
          v-mask="['(##) ####-####', '(##) #####-####']"
          label="Telefone/WhatsApp"
          placeholder="Digite seu número"
          class="data-hj-whitelist mt-2"
          outlined
          required
          id="tel"
          name="telefone1"
          autocomplete="off"
          data-hj-whitelist
        ></v-text-field>
      </ValidationProvider>
      <ValidationProvider v-slot="{ errors, valid }" name="E-mail" rules="required|email">
        <v-text-field
          v-model="form.email"
          :success="valid"
          :error-messages="errors"
          label="E-mail"
          placeholder="Digite seu e-mail"
          class="mt-2 data-hj-whitelist"
          outlined
          required
          id="email"
          name="email"
          data-hj-whitelist
        ></v-text-field>
      </ValidationProvider>

      <!-- <v-divider class="mt-4 mb-4"></v-divider>              -->

      <v-row>
        <v-col cols="12" class="d-flex justify-center">
          <!-- <v-btn large color="#0c26de" @click="submit" dark elevation="1" -->
          <v-btn
            x-large
            type="submit"
            dark
            elevation="2"
            class="btn-cta"
          >Continuar</v-btn>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" class="d-flex justify-end">
          <img :src="require('@/assets/img/CREDITEI-logo.png')" height="20" />
        </v-col>
      </v-row>
    </v-form>
  </ValidationObserver>
</template>

<script>
import { required, email } from "vee-validate/dist/rules";
import { BASE_URL } from '@/config/config'

import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode
} from "vee-validate";

setInteractionMode("eager");

extend("required", {
  ...required,
  message: "{_field_} não pode estar vazio."
});
extend("email", {
  ...email,
  message: "E-mail precisa ser válido."
});
extend("min", {
  validate(value, args) {
    return value.length >= args.length;
  },
  params: ["length"],
  message: "{_field_} deve ter pelo menos {length} caracteres."
});

import { mapActions } from "vuex";
export default {
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data: () => ({
    valid: false,
    date: null,
    menu: false,
    bar: 20,
    form: {
      nome: "",
      email: "",
      telefone1: "",
      ip: null,
      statusCadastro: null
    }
  }),

  methods: {
    ...mapActions(["sendLeadForm", "getIp"]),
    formSubmit() {
      this.$refs.observer.validate().then(retorno => {
        if (retorno) {
          window.fbq('track', 'Lead');
          this.form.statusCadastro = 1;
          this.sendLeadForm(this.form)
            .then(result => {
              let routeData = this.$router.resolve({
                name: "form-advanced",
                query: { j1: result.token, j2: result.id }
              });
              window.open(routeData.href, "_blank");
            })
            .catch(err => {
              console.log(err);
            });
        }
      });
    },
    async checkForm(e) { 
      try {
        var ret = await this.$refs.observer.validate();
        if ( ! ret ) {
          e.preventDefault();
          return false;
        }
        return ret;
      }
      catch(err) {
        e.preventDefault();
        return false;
      }
    }
  },
  computed: {
    urlNext() {
      return BASE_URL + '/v1/leads/create/redir';
    },
    urlOrigem() {
      return (window.location != window.parent.location) ? document.referrer : document.location.href
    }
  },
  mounted() {
  }
};
</script>
