<script>
import { mapActions } from "vuex";
import stepOne from "./stepOne/index";

export default {
  components: {
    appStepOne: stepOne
  },
  data: () => ({
    paletColor: {}
  }),

  methods: {
    ...mapActions(["addSocket"]),
    parseQuery(queryString) {
      var query = {};
      var pairs = (queryString[0] === "?"
        ? queryString.substr(1)
        : queryString
      ).split("&");
      for (var i = 0; i < pairs.length; i++) {
        var pair = pairs[i].split("=");
        query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || "");
      }
      return query;
    }
  },

  created() {
    /**
     * Configuracao de estilo inicial
     * Por aqui e pego os parametros de cores, enviado do iFrame
     * para o component
     */
    const queryString = window.location.search;

    this.paletColor = {};
    if (queryString) {
      this.paletColor = this.parseQuery(queryString);
    } else {
      this.paletColor.primaryColor = "ed9100"; //Button Background
      this.paletColor.secondaryColor = "ffffff"; //Card Background
    }

    console.log(window.location.search);
    console.log(queryString);
    console.log(this.paletColor);
  },
  computed: {
    cssVars() {
      return {
        "--bg-primary-color": `#${this.paletColor.primaryColor}`,
        "--bg-secondary-color": `#${this.paletColor.secondaryColor}`
      };
    }
  }
};
</script>

