<template>
  <div class="wrapper">
    <!-- :class="!imageData ? 'image-input' : 'image-input image-loaded'" -->
    <div
      :class="{'image-input': true, 'image-loaded': imageData, 'image-success': imageData && filestatus}"
      :style="{ 'background-image': `url(${imageData})` }"
      @click="chooseImage"
    >
      <span v-if="!imageData" class="placeholder">
        <v-icon x-large class="icon pt-4">mdi-camera</v-icon>Enviar imagem
        <b>
          {{placeholder}}
          <span v-if="ph2">{{ph2}}</span>
        </b>
      </span>

      <!-- <v-icon v-if="imageData" x-large class="icon-success" color="success">mdi-check-circle</v-icon> -->

      <input class="file-input" ref="fileInput" type="file" @input="onSelectFile" />
    </div>
    <div
      v-if="imageData"
      :class="filestatus ? 'label-success' : 'label-error'"
      style="text-align: center; width: 93%; position: relative; top: -5px; border-bottom-left-radius:4px;
      border-bottom-right-radius:4px;"
    >
      <b>
        {{placeholder}}
        <span v-if="ph2">{{ph2}}</span>
      </b>
    </div>
  </div>
</template>

<script>
export default {
  props: ["placeholder", "ph2", "filestatus"],
  data() {
    return {
      imageData: null
    };
  },
  mounte() {
    console.log(this.filestatus);
  },
  methods: {
    chooseImage() {
      this.$refs.fileInput.click();
    },

    onSelectFile() {
      const input = this.$refs.fileInput;
      const files = input.files;
      if (files && files[0]) {
        const reader = new FileReader();
        reader.onload = e => {
          this.imageData = e.target.result;
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
      }
    }
  }
};
</script>

